import React, { useEffect, useState } from "react";

import Table from "react-bootstrap/Table";
import Spinner from "react-bootstrap/Spinner";
import { Card, Row, Col, CardTitle, CardBody } from "reactstrap";
import { getLawyerRequest } from "../Common/Api-service";

// import { getLawyerList } from "../Common/Api-service";
import { Pagination } from "antd";

// import moment from "moment";

const LawyerList = () => {
  
  const [lawyerlistData, setLawyerData] = useState([]);
  const [showSpinner, setSpinner] = useState(true);
  const [totalCount, setTotalCount] = useState();
  const [currentPage, setCurrentPage] = useState(1);
  const [listData, setListData] = useState([]);

 

  const getAllLawyerRequest = () => {
    getLawyerRequest(currentPage)
      .then((res) => {
        // console.log(res.data.Data);
        console.log(res);
        const LawyerRequestData = res.data.data;
        setListData(LawyerRequestData);
        setSpinner(false);
        setTotalCount(res.data.totalCount);
      })
      .catch((err) => {
        console.log(err);
        setSpinner(false);
      });
  };

  useEffect(() => {
    getAllLawyerRequest();
  }, [currentPage]);

  const handleChangePage = (page) => {
    setCurrentPage(page);
  };

  return (
    <Row>
      <Col>
        {/* --------------------------------------------------------------------------------*/}
        {/* Card-1*/}
        {/* --------------------------------------------------------------------------------*/}
        <Card className="mt-5">
          <CardTitle tag="h6" className="border-bottom p-3 mb-0">
            <i className="bi bi-bell me-2"> </i>
            Users List
          </CardTitle>
          {showSpinner && (
            <Spinner
              style={{ margin: "5px auto" }}
              animation="border"
              variant="primary"
            />
          )}

          <CardBody>

            <Table striped bordered hover responsive>
              <thead>
                <tr>
                  <th className="font-weight-bold">#</th>
                  <th className="font-weight-bold">FirstName</th>
                  <th className="font-weight-bold">LastName</th>
                  <th className="font-weight-bold">Email Id</th>
                  <th className="font-weight-bold">Phone No</th>
                  <th className="font-weight-bold">isApproved</th>
                </tr>
              </thead>
              <tbody>
                {console.log("listData", listData)}

                {listData?.length >= 1 ? (
                  listData.map((data, i = 0) => {
                    return (
                      <tr key={data._id}>
                        <td>{i + 1}</td>
                        <td>{data.firstName}</td>
                        <td>{data.lastName}</td>
                        <td>{data.email}</td>
                        <td>{data.phoneNo}</td>
                        <td>{data.isApproved}</td>
                      </tr>
                    );
                  })
                ) : (
                  <tr>
                    <td colSpan={14} style={{ textAlign: "center" }}>
                      <strong>No Users available</strong>
                    </td>
                  </tr>
                )}
              </tbody>
            </Table>

            {console.log("totalcount", totalCount)}
            <Pagination
              onChange={(pg) => handleChangePage(pg)}
              defaultCurrent={currentPage}
              total={totalCount}
            />
          </CardBody>
        </Card>
      </Col>
    </Row>
  );
};

export default LawyerList;
